.button{
    background-color: rgba(188, 79, 175, 0.876);
    border-radius: 10px;
    font-size: 30px;
    padding: 20px;
    margin: auto;
    text-align: center;
    width: 350px;
    margin-bottom: 100px;
    margin-top: 20px;
    transition: all ease .7s;
}
.button:hover{
    background-color: rgba(237, 98, 221, 0.623);
    cursor: pointer;
    transform: scale(1.3);

}
.button a{
   text-decoration: none;
   color: white;
}


